@import url(https://fonts.googleapis.com/css?family=Rajdhani);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 650px;

  border: 2px solid gray;
  border-radius: 50px;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;

  background-size: 100%;
  position: relative;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
.app form {
  position: relative;
  margin-top: 25px;
  text-align: center;
}

.app input,
.app button {
  margin-left: 20px;
  margin-right: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #ffffff;
  transition: 0.3s;
}

.app button {
  cursor: pointer;
}
.app button:hover {
  background-color: grey;
  color: white;
  border-color: grey;
}

.result {
  font-family: "Rajdhani", sans-serif;
  text-align: center;
  margin: 25px;
  padding-top: 20px;
  border-top: 2px solid grey;
}
.left {
  width: 50%;
  float: left;
}

.right {
  width: 50%;
  float: right;
}
.sunrise {
  width: 50%;
  float: left;
}

.sunset {
  width: 50%;
  float: right;
  z-index: 1;
}
.result h3 {
  font-size: 40px;
  font-weight: bold;
  margin: 0px;
}
.result h4 {
  font-weight: normal;
  font-size: 20px;
}
img {
  margin-top: -100px;
  width: 300px;
  position: relative;
  z-index: -1;
}
.icon {
  margin-top: 100px;
}

